.panel-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 1.28rem;
  height: 3.84rem;
}

.panel-header__icon {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 1.70667rem;
  height: 1.70667rem;
  color: #78A4FA;
  text-align: center;
  background: #fff;
  box-shadow: 0 0.42667rem 1.70667rem 0 rgba(0, 0, 0, 0.11);
  border-radius: 0.21333rem;
}

.panel-header__icon .img {
  width: 0.768rem;
  height: 0.768rem;
}

.panel-header__title {
  margin-left: 0.68267rem;
  color: #78A4FA;
  font-size: 0.768rem;
  font-weight: bold;
}

.panel-body {
  min-height: -webkit-calc(100vh - 3.84rem);
  min-height: -webkit-calc(100vh - 180px);
  min-height: calc(100vh - 3.84rem);
  background: #fff;
  box-shadow: 0 0.55467rem 3.47733rem 0 rgba(0, 0, 0, 0.11);
}

.component-list {
  margin: 0.42667rem 1.28rem 0.85333rem;
}

.component-list__item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 2.56rem;
  border-bottom: 0.02133rem solid #e2ecf4;
}

.component-list__item .name {
  color: #1d1d26;
  font-size: 0.59733rem;
}

